*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins;
}

a{
  text-decoration: none;
  color: inherit;
  line-height: 1;
  cursor: pointer;

}

.container{
  padding-left: 10%;
  padding-right: 10%;
}

.btn{
  background: white;
  color: #212121;
  padding: 8px 25px;
  font-size: 16px;
  border-radius: 30px;
  cursor: pointer;
  border: 0;
  outline: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn img{
  width: 20px;
  margin-left: 10px;
}